<template>
    <div class="capability-container" ref="capabilityContainer">
        <div class="image-container">
            <img :src="url" alt="">
        </div>

        <div class="text-container">
            <h1>{{ title }}</h1>
            <p>{{ text }}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'PhCapabilityItem',
    props: {
        reversed: {
            type: Boolean,
            default: false,
        },
        title: {
            type: String,
            required: true,
        },
        text: {
            type: String,
            required: true,
        },
        imgSrc: {
            type: String,
            required: true,
        },
    },
    mounted() {
        if (this.reversed) {
            this.$refs.capabilityContainer.classList.add('reversed');
        }
    },
    computed: {
        url() {
            // eslint-disable-next-line import/no-dynamic-require, global-require
            const url = require(`@/assets/images/${this.imgSrc}`);
            return url;
        },
    },
};
</script>

<style scoped lang='sass'>
.capability-container
    @include flex-row(flex-start)
    align-items: center
    color: black !important
    margin: 20px 0

    & .text-container
        @include flex-column(center)
        width: 250px
        margin: 0 10px
        & h1
            font-weight: 800
            font-size: 1.2em
            color: #027AFF
            margin: 0
        & p
            font-size: 0.8em
            margin: 0
            opacity: 0.8

    & .image-container
        @include flex-column
        align-items: center
        background-color: white
        width: 65px
        height: 65px
        border-radius: 37.5px
        -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15)
        -moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15)
        box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15)
        & img
            width: 45px
            height: 45px

.reversed
    & .text-container
        order: 0
        text-align: right
    & .image-container
        order: 1
</style>



