<template>
    <div class="hero-container">
        <div class="text-column">
            <div class="text-container">
                <h1>Create amazing lightshows using Philips Hue®</h1>
                <p>
                    Hue Light Show allows you to create a beautiful lightshow by using
                    your existing Philips Hue® system. This is done in a simple to use interface.
                </p>

                <a class="appstore-link" href="">
                    <img
                        src="@/assets/images/download_app_store_badge.png"
                        alt="Download on the App Store"
                    >
                </a>
            </div>
        </div>

        <div class="image-container">
            <img src="@/assets/images/projects/PhLightShow/App_Preview.png" alt="">
        </div>
    </div>
</template>

<script>
export default {
    name: 'PhHero',
};
</script>

<style scoped lang='sass'>
.hero-container
    @include sm-max
        height: 130vh
    @include md
        height: 100vh

    max-width: 1500px

    @include sm-max
        @include flex-column(center)
        align-items: center
    @include md
        @include flex-row(center)

    margin-top: 20px

    & .text-column
        @include flex-column(center)
        align-items: center
        width: 50%
        & .text-container
            @include sm-max
                width: 90vw
                & h1
                    font-size: 2.8em
                    font-weight: 800
                & p
                    margin-top: 1.5em
                    font-size: 1em
                & .appstore-link
                    & img
                        width: 150px
            @include md
                width: 400px
                & h1
                    font-size: 3em
                    font-weight: 800
                & p
                    margin-top: 2em
                    font-size: 1.1em
                & .appstore-link
                    & img
                        width: 150px

    & .image-container
        @include sm-max
            // text-align: center
            width: 90vw
            margin: 40px 0 0 0
            & img
                height: 60vw
        @include md
            @include flex-column(center)
            align-items: center
            width: 50%
            & img
                height: 600px
</style>



