<template>
    <div class="programming-language-container">
        <div class="icon-container" v-for="(color, iconName) in iconsAndColors" :key="iconName">
            <programming-language-icon
                :iconName="iconName"
                :borderColor="color"
            />
        </div>
    </div>
</template>

<script>
import programmingLanguageIcon from '@/components/aboutMe/ProgrammingLanguageIcon';

export default {
    name: 'PortfolioProgrammingLanguages',
    data() {
        return {
            iconsAndColors: {
                'swift-icon.png': '#F25637',
                'c-icon.png': '#5C6BC0',
                'html5-icon.png': '#E44F26',
                'keras-icon.png': '#CF0001',
                'python-icon.png': '#367EF0',
                'sql-icon.png': '#0171C5',
                'vue-icon.png': '#40B883',
            },
        };
    },
    components: {
        programmingLanguageIcon,
    },
};
</script>


<style scoped lang='sass'>
@import '../../assets/styles/global.sass'

.programming-language-container
    @include unselectable
    @include flex-row(space-between)
    flex-wrap: wrap

.icon-container
    margin: 20px
</style>



