<template>
    <div>
        <b-container>
            <b-row>
                <b-col cols="12" md="6" class="sm-max-center-horizontal md-flex-end">
                    <img
                        class="profile-image"
                        src="@/assets/images/profile-image.png"
                        alt="Marc Wiggerman"
                    />
                </b-col>

                <b-col cols="12" md="6" class="sm-max-center-horizontal md-center-vertical">
                    <portfolio-summary/>
                </b-col>
            </b-row>
        </b-container>

        <portfolio-programming-languages/>
    </div>
</template>

<script>
import portfolioSummary from '@/components/aboutMe/PortfolioSummary';
import portfolioProgrammingLanguages from '@/components/aboutMe/PortfolioProgrammingLanguages';

export default {
    name: 'AboutMeSection',
    components: {
        // portfolioImage,
        portfolioSummary,
        portfolioProgrammingLanguages,
    },
};
</script>


<style scoped lang='sass'>
@import '../../assets/styles/global.sass'

.profile-image
    object-fit: contain

    @include sm-max
        width: 50%
    @include md
        width: 100%
        max-width: 300px

.sm-max-center-horizontal
    @include sm-max
        @include flex-row(center)

.md-center-vertical
    @include md
        @include flex-column(center)

.md-flex-end
    @include md
        @include flex-row
        justify-content: flex-end
</style>



