<template>
    <div class="container" ref="contentContainer" @click="$emit('clicked', title)">
        <img class="logo-image" :src="url" :alt="imageAlt">
        <div class="text-container">
            <h1>{{ title }}</h1>
            <h2>{{ subTitle }}</h2>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ProjectCard',
    data() {
        return {
        };
    },
    props: {
        backgroundColor: {
            type: String,
            required: true,
        },
        textColor: {
            type: String,
            required: true,
        },
        title: {
            type: String,
            required: true,
        },
        subTitle: {
            type: String,
            required: true,
        },
        imageSrc: {
            type: String,
            required: true,
        },
        imageAlt: {
            type: String,
            required: true,
        },
    },
    mounted() {
        const container = this.$refs.contentContainer;
        container.style.backgroundColor = this.backgroundColor;
        container.style.color = this.textColor;
    },
    computed: {
        url() {
            // eslint-disable-next-line import/no-dynamic-require, global-require
            const url = require(`@/assets/images/projects/${this.imageSrc}`);
            return url;
        },
    },
};
</script>

<style scoped lang='sass'>
@import '../../assets/styles/global.sass'

.container
    @include unselectable
    @include sm-max
        margin: 10px 0px

    @include flex-column(center)
    align-items: center

    position: relative
    width: 100%
    max-width: 100% !important
    height: 250px
    border-radius: 4px

    cursor: pointer

    -webkit-box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.25)
    -moz-box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.25)
    box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.25)

    transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    &:hover
        -webkit-transform: scale(1.05)
        -moz-transform: scale(1.05)
        transform: scale(1.05)

.logo-image
    @include undraggable

    height: 50%
    width: 60%
    object-fit: contain
    margin: 10px 0

.text-container
    @include flex-column(center)
    align-items: center
    text-align: center
    & h1
        font-size: 20px

    & h2
        font-size: 15px

</style>



