<template>
    <div class="privacy-container">
        <div class="header-container">
            <icon class='icon' name='hand-peace'/>
            <h1>Privacy policy</h1>
        </div>

        <p>
            Marc Wiggerman Software Developments and it's affiliated applications does not store
            any user data, logs or other data that can be seen as personal data.
            <br/>
            <br/>
            <b>- Marc Wiggerman</b>
        </p>

        <button @click="$router.push({name: 'Home'})">
            Thanks!
        </button>
    </div>

</template>

<script>
import icon from 'vue-awesome/components/Icon';

export default {
    name: 'PrivacyPolicy',
    components: {
        icon,
    },
};
</script>

<style lang="sass" scoped>
.privacy-container
    @include flex-column(center)
    height: 100vh
    margin: 0 20%
    text-align: center

    & p
        margin: 40px 0

    & button
        height: 50px
        background-color: $system-blue
        border: none
        border-radius: 12px
        color: white
        font-weight: bold

        outline: none

.header-container
    @include flex-row(center)
    align-items: center

    & .icon
        height: 30px
        width: 30px
        margin: 0 20px
</style>



