<template>
    <b-container class="b-container">
        <b-row>
            <b-col cols="12" md="12" class="right-padding left-padding">
                <project-card
                    backgroundColor="#000000"
                    textColor="#FFFFFF"
                    title="Hue Light Show"
                    subTitle="App Store App"
                    imageSrc="huelightshow_logo.png"
                    imageAlt="Hue Light Show Project"
                    @clicked="projectClicked"
                />
            </b-col>
        </b-row>

        <b-row>
            <b-col cols="12" md="8" class="right-padding">
                <project-card
                    backgroundColor="#12192F"
                    textColor="#FFFFFF"
                    title="WWDC 2019"
                    subTitle="Scholarship Winner"
                    imageSrc="wwdc19_logo.jpg"
                    imageAlt="WWDC 2019 logo"
                    @clicked="projectClicked"
                />
            </b-col>
            <b-col cols="12" md="4" class="left-padding">
                <project-card
                    backgroundColor="#FFFFFF"
                    textColor="#868686"
                    title="Bachelor Thesis"
                    subTitle="University of Amsterdam"
                    imageSrc="abnAmro_logo.png"
                    imageAlt="Abn Amro logo"
                    @clicked="projectClicked"
                />
            </b-col>
        </b-row>

        <b-row>
            <b-col cols="12" md="4" class="right-padding">
                <project-card
                    backgroundColor="#FFFFFF"
                    textColor="#868686"
                    title="Computer Vision Project"
                    subTitle="University of Amsterdam"
                    imageSrc="nlr_logo.png"
                    imageAlt="NLR logo"
                    @clicked="projectClicked"
                />
            </b-col>
            <b-col cols="12" md="8" class="left-padding">
                <project-card
                    backgroundColor="#F8F8F8"
                    textColor="#868686"
                    title="ML x AI Project"
                    subTitle="University of Amsterdam"
                    imageSrc="oracle_logo.png"
                    imageAlt="Oracle logo"
                    @clicked="projectClicked"
                />
            </b-col>
        </b-row>

        <b-row>
            <b-col cols="12" md="8" class="right-padding">
                <project-card
                    backgroundColor="#ffffff"
                    textColor="#3D3E3D"
                    title="WWDC 2017"
                    subTitle="Scholarship Winner"
                    imageSrc="wwdc17_logo.png"
                    imageAlt="WWDC 2017 logo"
                    @clicked="projectClicked"
                />
            </b-col>
            <b-col cols="12" md="4" class="left-padding">
                <project-card
                    backgroundColor="#000000"
                    textColor="#FFFFFF"
                    title="Receipt Butler"
                    subTitle="App Store App"
                    imageSrc="receiptButler_logo.png"
                    imageAlt="Receipt Butler Project"
                    @clicked="projectClicked"
                />
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
import projectCard from '@/components/projects/ProjectCard';

export default {
    name: 'ProjectSection',
    data() {
        return {
        };
    },
    components: {
        projectCard,
    },
    methods: {
        projectClicked(title) {
            this.$emit('projectClicked', title);
        },
    },
};
</script>

<style lang="sass">
.left-padding
    @include md
        padding-right: 0 !important
        padding-left: 15px

.right-padding
    @include md
        padding-right: 15px
        padding-left: 0 !important

.b-container
    padding: 0
    margin: 0

    max-width: 100% !important

.card-container
    @include flex-row(center)
    flex-wrap: wrap

    background-color: gray

.row
    @include md
        margin: 30px 0
</style>



