<template>
    <div class="capabilities-container">
        <h1>Features</h1>

        <div class="capabilities-columns">
            <div class="column">
                <ph-capability-item
                    title="Philips Hue®"
                    text="Connects to the Philips Hue® system"
                    :reversed="true"
                    imgSrc="projects/PhLightShow/capabilities/philips_hue_logo.png"
                />
                <ph-capability-item
                    title="iCloud Drive®"
                    text="All project will be svaed to iCloud"
                    :reversed="true"
                    imgSrc="projects/PhLightShow/capabilities/cloud.png"
                />
            </div>
            <div class="image-container">
                <img src="@/assets/images/projects/PhLightShow/Highlight.png" alt="">
            </div>
            <div class="column">
                <ph-capability-item
                    title="Add Music"
                    text="Add music to your show by selecting one from Apple Music"
                    imgSrc="projects/PhLightShow/Apple_Music_Icon.png"
                />
                <ph-capability-item
                    title="Connects to Airplay"
                    text="Connect to AirPlay for the ultimate experience"
                    imgSrc="projects/PhLightShow/capabilities/airplay.png"
                />
            </div>
        </div>
    </div>
</template>

<script>
import PhCapabilityItem from '@/components/phLightShow/Widgets/PhCapabilityItem';

export default {
    name: 'PhCapabilities',
    components: {
        PhCapabilityItem,
    },
};
</script>

<style scoped lang='sass'>
.capabilities-container
    @include sm-max
        height: 120vh
    @include md
        height: 80vh

    @include flex-column(center)
    align-items: center

    background-color: white
    color: black
    & h1
        font-weight: bold

.capabilities-columns
    max-width: 1500px
    @include sm-max
        margin: 20px 10px
        & .column
            @include flex-column(center)
            width: 90%
    @include md
        margin: 20px 10px
        @include flex-row(center)
        & .column
            @include flex-column(center)
            width: 30%
            align-items: center

    & .image-container
        @include sm-max
            display: none
        @include md
            @include flex-column(center)
            width: 40%
            padding: 10px
            & img
                width: 100%
</style>



