<template>
    <div class="rb-container">
        <ph-header/>
        <ph-hero/>
        <ph-music-animation/>
        <ph-capabilities/>
        <ph-copyright-footer/>
    </div>
</template>

<script>
import icon from 'vue-awesome/components/Icon';

/* eslint-disable import/no-unresolved */
import PhHeader from '@/components/phLightshow/PhHeader';
import PhHero from '@/components/phLightshow/PhHero';
import PhMusicAnimation from '@/components/phLightshow/PhMusicAnimation';
import PhCapabilities from '@/components/phLightshow/PhCapabilities';
import PhCopyrightFooter from '@/components/phLightshow/PhCopyrightFooter';

export default {
    name: 'PrivacyPolicy',
    components: {
        icon,
        PhHeader,
        PhHero,
        PhMusicAnimation,
        PhCapabilities,
        PhCopyrightFooter,
    },
};
</script>

<style lang="sass" scoped>
.rb-container
    background-color: black
    color: white
    font-family: Raleway
    margin: 0 -20px
    width: 100vw
    overflow-x: hidden
</style>



