<template>
    <div>
        <navigation-bar id="welcome"/>
        <div class="main-content-container">
            <welcome/>

            <h2 id="projects">Projects</h2>
            <project-section
                @projectClicked="projectClicked"
            />

            <h2 id="about_me">About Me</h2>
            <about-me-section/>

            <h2 id="contact">Contact Me</h2>
        </div>
        <footer-view/>
    </div>
</template>

<script>
import navigationBar from '@/components/global/NavigationBar';
import welcome from '@/components/home/Welcome';
import projectSection from '@/components/projects/ProjectSection';
import aboutMeSection from '@/components/aboutMe/AboutMeSection';
import footerView from '@/components/footer/FooterView';

export default {
    name: 'Home',
    data() {
        return {
        };
    },
    methods: {
        projectClicked(title) {
            switch (title) {
            case 'Receipt Butler':
                this.$router.push({ name: 'ReceiptButler' });
                break;
            case 'Hue Light Show':
                window.scrollTo(0, 0);
                this.$router.push({ name: 'PHLightShow' });
                break;
            default:
                break;
            }
        },
    },
    components: {
        navigationBar,
        welcome,
        projectSection,
        aboutMeSection,
        footerView,
    },
};
</script>

<style lang="sass" scoped>
.main-content-container
    @include sm-max
        margin: 0 10px
    @include md
        margin: 0 10%
        max-width: 1140px
        margin: 0 auto

    & h2
        margin-top: 100px

.overlay-container
    width: 100%
    height: 100%
    z-index: 100

    position: fixed
    top: 0px
    left: 0px

</style>



