<script>
export default {
    name: 'PhMusicAnimation',
};
</script>

<style scoped lang='sass'>
.music-container
    height: 100vh
    @include flex-column(center)
    align-items: center
    .text-container
        @include flex-column(center)
        align-items: center
        position: absolute
        & h1
            font-weight: 800
            font-size: 3.4em
        & img
            width: 50px
            height: 50px
    #sound-wave-img
        @include sm-max
            width: 200vw
        @include md
            width: 100vw
        opacity: 0.3
</style>



