<template>
    <div class="rb-container">
        <img
            class="logo-image"
            src="@/assets/images/projects/receiptButler_logo.png"
            alt="Receipt Butler"
        />
        <h1>Receipt Butler</h1>
        <h2>Scan, Store and Search Receipts</h2>

        <p>With Receipt Butler you can store the receipt you received with every purchase
        directly on your device! Every receipt is analysed on device to extract the products so
        you can search them on a later moment.</p>

        <h2>Website coming soon</h2>

        <div class="contact-container">
            <p><b>For Questions Contact:</b></p>
            <p>mwiggerm [at] icloud [dot] com</p>
        </div>
    </div>

</template>

<script>
import icon from 'vue-awesome/components/Icon';

export default {
    name: 'PrivacyPolicy',
    components: {
        icon,
    },
};
</script>

<style lang="sass" scoped>
.rb-container
    font-family: 'Montserrat', sans-serif
    text-align: center
    background-color: black
    color: white
    height: 100vh
    margin: 0 -20px

    @include md
        padding: 20vw

    @include flex-column(center)

    & h1
        font-weight: 800
        font-size: 40px
        @include xs-max
            font-size: 30px
    & h2
        font-size: 20px
        @include xs-max
            font-size: 15px

    & p
        margin: 20px 40px
        @include xs-max
            font-size: 13px

.contact-container
    & p
        margin: 0px 10px

.logo-image
    object-fit: contain
    width: 100%
    height: 150px
    margin: 20px 0
</style>



